<template>
  <div id="editor">

  </div>
</template>

<script>
  import WangEditor from 'wangeditor'
  import axios from 'axios';

  export default {
    name: "editor",
    props: {
      value: {
        type: String
      }
    },
    data() {
      return {
        editor: null,
        DataValue: '',
        token: ''
      }
    },
    methods: {
      getToken() {
        this.$axios.qiniuGet().then(res => {
          this.token = res.data;
        })
      }
    },
    created() {
      let _this=this;
      this.getToken();
      let that = this;
      this.$nextTick(() => {
        this.editor = new WangEditor('#editor');
        // 或者 var editor = new E( document.getElementById('editor') )

        this.editor.customConfig.customUploadImg = function (files, insert) {
          let form = new FormData();
          form.append("file", files[0]);
          form.append("token", _this.token);
          axios.post("https://upload-z1.qiniup.com", form).then(res => {
            console.log(res);
            insert(res.data.url);
          })
        };

        this.editor.customConfig.onchange = function (html) {
          // html 即变化之后的内容
          that.$emit('input', html);
          that.$emit('getText', that.editor.txt.text())
        };
        this.editor.customConfig.zIndex = 100;
        this.editor.create();

        // document.getElementById('editor').addEventListener('keyup', function () {
        //     // 如果未配置 editor.customConfig.onchange，则 editor.change 为 undefined
        //     that.editor.change && that.editor.change()
        // })

      })
    },
    watch: {
      value(val) {
        this.DataValue = val;
        this.editor.txt.html(val)
      }
    }
  }
</script>

<style scoped>

</style>



